.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  padding: 1rem;
}
.logo {
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.navbar-brand a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
}

.navbar-links {
  list-style-type: none;
  display: flex;
  gap: 3rem;
  margin-right: 10%;
}

.navbar-links li a {
  color: white;
  text-decoration: none;
  font-size: larger;
}

.navbar-links li a:hover {
  color: #e09419;
}
.card {
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  max-width: 1200px;
  margin: 1rem;
}

.card-content {
  padding: 1rem;
}
.card-image {
  height: 250px;
  width: 200px;
  float: right;
  margin-bottom: 5%;
  margin-right: 2%;
  border-radius: 50%;
}
.main-title {
  display: flex;
  justify-content: center;
}

.card-title {
  padding: 30px;
  font-size: 1.75rem;
  margin-bottom: -4%;
}

.card-description {
  font-size: 1.25rem;
  color: #555;
  float: left;
  width: 50%;
  padding: 30px;
}
.projects {
  padding: 1%;
  margin-left: -1%;
  margin-right: -1%;
}

.project-A {
  font-size: 1rem;
  color: rgb(0, 0, 0);
  width: 30;
}
.film {
  height: 65%;
  width: 100%;
  padding-bottom: 5%;
}
.insta {
  height: 3%;
  width: 3%;
}
.container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  cursor: pointer;
}

.work-A {
  margin-top: 4%;
  border: 1px solid #ddd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  max-width: 380px;
  height: 500px;
}
.work-A:hover {
  background-color: rgba(237, 230, 230, 0.959);
  text-decoration: none;
}
.option-A {
  margin-top: 380%;
  height: 50%;
  width: 40%;
}
.web {
  margin-left: 2%;
  height: 25%;
  width: 45%;
  border-radius: 10%;
  border: #333;
}
.web-A {
  margin-left: 5%;
  height: 25%;
  width: 45%;
  border-radius: 10%;
}
.touch {
  margin-top: 4%;
  margin-left: 10%;
  background-color: rgb(18, 18, 18);
  width: 20%;
  border-radius: 15px;
}
.btn {
  color: #ffffff;
  display: flex;
  justify-content: center;
  font-size: x-large;
  font-weight: 500;
}
.touch:hover {
  background-color: hsl(13, 73%, 28%);
}

.Insta {
  margin-top: 20%;
  border-radius: 200%;
  height: 50%;
  width: 50%;
}
.pro {
  height: 20%;
  width: 20%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-left: 11.5%;
}
.Linked {
  margin-top: 20%;
  border-radius: 200%;
  height: 45%;
  width: 45%;
}
.likho {
  height: 30%;
  max-width: 30%;
}
.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.contactus {
  display: flex;
  justify-content: center;
  align-items: center;
}
.name {
  display: flex;
  text-align: center;
  margin-bottom: 10%;
  border-radius: 50px;
  width: 100%;
  height: 20px;
  padding: 12px 12px;
}
.mail {
  display: flex;
  text-align: center;
  border-radius: 50px;
  margin-bottom: 10%;
  width: 100%;
  height: 20px;
  padding: 12px 12px;
}
.number {
  display: flex;
  text-align: center;
  border-radius: 50px;
  /* background-color: #e09419;  */
  height: 5%;
  width: 15;
  margin-bottom: 10%;
  width: 100%;
  height: 20px;
  padding: 12px 12px;
}
.line {
  font-size: xx-large;
  margin-bottom: 10%;
  display: flex;
  justify-content: center;
}
.full-form {
  border-radius: 20px;
  margin-left: 30%;
  margin-top: 5%;
  padding-left: 0%;
  padding-top: 2%;
  max-width: 40%;
  height: 100%;
  background-color: #afa89d;
}
.suggestion {
  text-align: center;
  width: 110%;
  height: 100px;
  padding: 12px 20px;
  margin-bottom: 10%;
  border-radius: 100px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  resize: none;
}
.submit {
  margin-left: 38%;
  margin-bottom: 10%;
  display: inline-block;
  padding: 15px 15px;
  font-size: 15px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #04AA6D;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}
.div {
  font-size: x-large;
  text-align: center;
}
